/*******************************/
/********* General CSS *********/
/*******************************/
body {
  color: #666666;
  background: #ffffff;
  font-family: 'Urbanist', sans-serif;
  font-weight: 600;
}

h1,
h2, 
h3, 
h4,
h5, 
h6 {
  font-weight: 100;
}

a {
  color: #454545;
  transition: .3s;
}

a:hover,
a:active,
a:focus {
  color: #333333;
  outline: none;
  text-decoration: none;
}

.btn:focus {
  box-shadow: none;
}

.wrapper {
  position: relative;
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  background: #ffffff;
}

.back-to-top {
  position: fixed;
  display: none;
  background: #00539C;
  color: #000000;
  width: 44px;
  height: 44px;
  text-align: center;
  line-height: 1;
  font-size: 22px;
  border-radius: 5px;
  right: 15px;
  bottom: 15px;
  transition: background 0.5s;
  z-index: 9;
}

.back-to-top:hover {
  color: #ffffff;
  background: #fe4b7e;
}

.back-to-top i {
  padding-top: 10px;
}


/*******************************/
/********** Header CSS *********/
/*******************************/
.header {
  position: relative;
  width: 100%;
  padding: 15px 0 0 0;
  background: #fe4b7e;
}

.header.home {
  margin-bottom: 45px;
  background: linear-gradient(rgba(0, 83, 156, 0.8), rgba(0, 83, 156, .4), rgba(0, 83, 156, .3)), 
  url(./assets/img/header.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

@media (min-width: 992px) {
  .header .row {
      padding: 0 60px;
  }
}

.header .header-top {
  padding-bottom: 15px;
}

.header .brand {
  position: relative;
}

.header .brand img {
  width: 100%;
  max-width: 200px;
  height: 100%;
  max-height: 150px;
}

.header .topbar {
  position: relative;
  text-align: right;
}

.header .topbar .topbar-col {
  display: inline-block;
  padding: 0 15px;
  margin-bottom: 15px;
}

.header .topbar-col a {
  color: #ffffff;
  font-size: 13px;
}

.header .topbar-col a i {
  margin-right: 10px;
  color: #fe4b7e;
}

.header .topbar-social {
  position: relative;
}

.header .topbar-social a {
  display: inline-block;
}

.header .topbar-social a i {
  margin-right: 10px;
  font-size: 18px;
  color: #ffffff;
}

.header .topbar-social a:last-child i {
  margin: 0;
}

.header .topbar-social a:hover i {
  color: #fe4b7e;
}

@media (min-width: 992px) {
  .header .navbar {
      background: transparent !important;
  }
  
  .header .navbar-brand {
      display: none;
  }
  
  .header .navbar-light a.nav-link,
  .header .navbar-light .show > .nav-link {
      position: relative;
      padding: 8px 10px;
      color: #ffffff;
      font-size: 20px;
      z-index: 1;
  }
  
  .header .navbar-light a.nav-link:hover,
  .header .navbar-light a.nav-link.active {
      color: #fe4b7e;
  }
}

@media (max-width: 992px) {
  .header .brand {
      text-align: center;
      margin-bottom: 15px;
  }
  
  .header .topbar {
      text-align: center;
      margin-bottom: 15px;
  }
  
  .header .topbar .topbar-col {
      margin: 0;
  }
  
  .header .topbar .topbar-social {
      margin-top: 5px;
  }
  
  .header .navbar {
      background: #ffffff !important;
      border: none;
      z-index: 1;
  }
  
  .header a.nav-link {
      padding: 5px 0;
  }
  
}

.header .navbar .btn {
  position: relative;
  margin-left: 15px;
  padding: 12px 20px 10px 20px;
  font-size: 13px;
  text-transform: uppercase;
  color: #00539C;
  border: none;
  border-radius: 5px;
  background: #ffffff;
  transition: .3s;
}

.header .btn:hover {
  color: #000000;
  background: #fe4b7e;
}

.header .navbar .btn i {
  font-size: 16px;
  margin-right: 8px;
}

@media (max-width: 992px) {
  .header .navbar {
      border-radius: 5px;
  }
  
  .header .navbar .btn {
      display: none;
  }
}

.header .hero {
  padding-top: 60px;
  padding-bottom: 60px;
}

.header .hero h2 {
  margin: 0;
  font-size: 60px;
  font-weight: 400;
  color: #ffffff;
}

.header .hero h2 span {
  color: #fe4b7e;
}

.header .hero p {
  margin: 10px 0 20px 0;
  font-size: 22px;
  color: #ffffff;
}

.header .hero a.btn {
  position: relative;
  margin-top: 10px;
  padding: 15px 30px;
  font-size: 16px;
  text-transform: uppercase;
  color: #00539C;
  border: none;
  border-radius: 5px;
  background: #ffffff;
  transition: .3s;
}

.header .hero a.btn:hover {
  color: #000000;
  background: #fe4b7e;
}

/*******************************/
/******* Page Header CSS *******/
/*******************************/
.page-header {
  position: relative;
  margin-bottom: 45px;
  padding: 90px 0;
  text-align: center;
  background: #FFD662;
}

.page-header h2 {
  color: #000000;
  font-size: 60px;
  font-weight: 100;
  text-transform: uppercase;
  margin-bottom: 15px;
}

.page-header a {
  position: relative;
  padding: 0 12px;
  font-size: 22px;
  font-weight: 300;
  color: #00539C;
}

.page-header a:hover {
  color: #000000;
}

.page-header a::after {
  position: absolute;
  content: "/";
  width: 8px;
  height: 8px;
  top: -2px;
  right: -7px;
  text-align: center;
  color: #666666;
}

.page-header a:last-child::after {
  display: none;
}


/*******************************/
/******* Section Header ********/
/*******************************/
.section-header {
  position: relative;
  width: 100%;
  max-width: 550px;
  text-align: center;
  margin: 0 auto;
  margin-bottom: 60px;
  padding-bottom: 15px;
}

.section-header::before {
  position: absolute;
  content: "";
  width: 100px;
  height: 3px;
  left: calc(50% - 50px);
  bottom: 0;
  background: linear-gradient(to left, #FFD662, #00539C, #FFD662);
  border-radius: 100%;
}

.section-header::after {
  position: absolute;
  content: "";
  width: 20px;
  height: 9px;
  left: calc(50% - 10px);
  bottom: -3px;
  background: #00539C;
  border-radius: 5px;
}

.section-header p {
  margin-bottom: 10px;
  font-size: 20px;
}

.section-header h2 {
  color: #00539C;
  font-size: 50px;
  font-weight: 100;
}

.section-header.left {
  text-align: left;
}

.section-header.left::before {
  width: 60px;
  left: 0;
  background: linear-gradient(to left, #FFD662, #00539C, #00539C);
  border-radius: 0 100% 100% 0;
}

.section-header.left::after {
  left: 0;
  border-radius: 5px;
}


/*******************************/
/********** About CSS **********/
/*******************************/
.about {
  position: relative;
  width: 100%;
  padding: 45px 0;
}

.aprops {
  padding: 30px;
}

.aprops h2 {
  text-align: center;
  font-weight: 600;
  color: #00539C;
}

.aprops p {
  text-align: center;
}

.about .about-img img {
  width: 100%;
  border-radius: 5px;
}

.about .about-text h2 {
  position: relative;
  color: #fe4b7e;
  font-size: 50px;
  margin-bottom: 15px;
}

.about .about-text h2 span {
  font-size: 120px;
}

.about .about-text p {
  font-size: 16px;
  margin-bottom: 30px;
}

.about .about-text a.btn {
  padding: 15px 35px;
  font-size: 16px;
  text-transform: uppercase;
  color: #ffffff;
  border: none;
  border-radius: 5px;
  background: #00539C;
  transition: .3s;
}

.about .about-text a.btn:hover {
  color: #000000;
  background: #fe4b7e;
}

@media (max-width: 767.98px) {
  .about .about-img img {
      margin-bottom: 30px;
  }
  .header .hero h2 {
    font-size: 40px;
    font-weight: 300;
  }
}

/*******************************/
/********* Service CSS *********/
/*******************************/
.service {
  position: relative;
  width: 100%;
  padding: 45px 0 15px 0;
}

.service .service-item {
  position: relative;
  width: 100%;
  text-align: center;
  background: #ffffff;
  border-radius: 5px;
  margin-bottom: 30px;
  box-shadow: 0 0 30px rgba(0, 0, 0, .1);
}

.service .service-item img {
  width: 100%;
  border-radius: 5px 5px 0 0;
}

.service .service-item h3 {
  margin: 0;
  padding: 25px 15px 15px 15px;
  font-size: 15px;
  font-weight: 900;
  color: #00539C;
}

.service .service-item p {
  margin: 0;
  padding: 0 25px 25px 25px;
  font-size: 15px;
  font-weight: 600px;
  color: #fe4b7e;
}

.service .service-item a.btn {
  position: relative;
  margin-bottom: 30px;
  padding: 10px 20px;
  font-size: 14px;
  text-transform: uppercase;
  color: #000000;
  border: none;
  border-radius: 5px;
  background: #fe4b7e;
  transition: .3s;
}

.service .service-item:hover a.btn {
  color: #ffffff;
  background: #00539C;
}

/*******************************/
/********* Feature CSS *********/
/*******************************/
.feature {
  position: relative;
  width: 100%;
  padding: 45px 0 15px 0;
}

.feature .section-header {
  margin-bottom: 30px;
}

.feature a.btn {
  margin-top: 10px;
  padding: 15px 35px;
  font-size: 16px;
  text-transform: uppercase;
  color: #000000;
  border: none;
  border-radius: 5px;
  background: #fe4b7e;
  transition: .3s;
}

.feature a.btn:hover {
  color: #ffffff;
  background: #00539C;
}

.feature h1 {
  text-align: center;
  margin-bottom: 50px;
}

.feature .feature-item {
  margin-bottom: 50px;
}

.feature .feature-item i {
  width: 100%;
  border-radius: 5px;
  margin-top: 10px;
  font-size: 22px;
  color: #00539C;
}

.feature .feature-item p {
  margin: 0;
  font-size: 16px;
}

@media (max-width: 767.98px) {
  .feature a.btn {
      margin-bottom: 45px;
  }
}


/*******************************/
/******* Testimonial CSS *******/
/*******************************/
.testimonial {
  position: relative;
  padding: 45px 0;
}

.testimonial .testimonial-item {
  position: relative;
  margin: 0 15px;
  text-align: center;
}

.testimonial .testimonial-img {
  position: relative;
  margin-bottom: 15px;
  z-index: 1;
}

.testimonial .testimonial-item img {
  margin: 0 auto;
  width: 150px;
  border: 15px solid #ffffff;
  border-radius: 100px;
}

.testimonial .testimonial-content {
  position: relative;
  margin-top: -90px;
  padding: 100px 25px 25px 25px;
  text-align: center;
  background: #FFD662;
  border-radius: 5px;
}

.testimonial .testimonial-item p {
  color: #000000;
  font-size: 16px;
  font-weight: 400;
}

.testimonial .testimonial-content h3 {
  color: #000000;
  font-size: 18px;
  font-weight: 400;
  text-transform: uppercase;
  margin-bottom: 5px;
}

.testimonial .testimonial-content h4 {
  font-size: 14px;
  font-weight: 400;
  margin-bottom: 0;
}

.testimonial .owl-nav,
.testimonial .owl-dots {
  margin-top: 15px;
  text-align: center;
}

.testimonial .owl-dot {
  display: inline-block;
  margin: 0 5px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #FFD662;
}

.testimonial .owl-dot.active {
  background: #00539C;
}


/*******************************/
/*********** Blog CSS **********/
/*******************************/
.blog {
  position: relative;
  width: 100%;
  padding: 45px 0 15px 0;
}

.blog .blog-item {
  position: relative;
  width: 100%;
  border-radius: 5px;
  margin-bottom: 30px;
  background: #ffffff;
  box-shadow: 0 0 30px rgba(0, 0, 0, .1);
}

.blog .blog-item img {
  width: 100%;
  border-radius: 5px 5px 0 0;
}

.blog .blog-item h3 {
  margin: 0;
  padding: 25px 30px 5px 30px;
  font-size: 22px;
  font-weight: 300;
  color: #00539C;
}

.blog .blog-item .meta {
  padding: 0 30px 15px 30px;
}

.blog .blog-item .meta * {
  display: inline-block;
  margin: 0;
  padding: 0;
  font-size: 14px;
  color: #999999;
}

.blog .blog-item .meta i {
  margin: 0 2px 0 10px;
}

.blog .blog-item .meta i:first-child {
  margin-left: 0;
}

.blog .blog-item p {
  padding: 0 30px;
  font-size: 16px;
}

.blog .blog-item a.btn {
  position: relative;
  margin: 5px 30px 30px 30px;
  padding: 10px 20px;
  font-size: 14px;
  text-transform: uppercase;
  color: #000000;
  border: none;
  border-radius: 5px;
  background: #FFD662;
  transition: .3s;
}

.blog .blog-item:hover a.btn {
  color: #ffffff;
  background: #00539C;
}

.blog .blog-item a.btn:hover {
  color: #000000;
  background: #FFD662;
}


/*******************************/
/******* Call to Action ********/
/*******************************/
.call-to-action {
  position: relative;
  margin: 45px 0;
  padding: 60px 0;
  background: #00539C;
}

.call-to-action h2 {
  color: #ffffff;
  font-size: 45px;
  font-weight: 100;
  text-transform: uppercase;
}

.call-to-action p {
  margin: 0;
  color: #ffffff;
  font-size: 20px;
  font-weight: 300;
}

.call-to-action a {
  float: right;
  height: 60px;
  padding: 16px 35px 14px 35px;
  font-size: 18px;
  font-weight:400;
  text-transform: uppercase;
  color: #000000;
