  }
}


/*******************************/
/********** Story CSS **********/
/*******************************/
.story {
  position: relative;
  width: 100%;
  padding: 45px 0 15px 0;
}

.story .container {
  max-width: 960px;
}

.story .story-container {
  position: relative;
  width: 100%;
}

.story .story-end,
.story .story-start,
.story .story-year {
  position: relative;
  width: 100%;
  text-align: center;
  z-index: 1;
}

.story .story-end p,
.story .story-start p,
.story .story-year p {
  display: inline-block;
  width: 100px;
  margin: 0;
  padding: 10px 0;
  text-align: center;
  background: #FFD662;
  border: 1px solid #00539C;
  border-radius: 5px;
  color: #000000;
  font-size: 16px;
  text-transform: uppercase;
}

.story .story-year {
  margin: 30px 0;
}

.story .story-continue {
  position: relative;
  width: 100%;
  padding: 60px 0;
}

.story .story-continue::before {
  position: absolute;
  content: "";
  width: 3px;
  height: 100%;
  top: 0;
  left: 50%;
  margin-left: -1.5px;
  background: #FFD662;
  border-left: 1px solid #00539C;
  border-right: 1px solid #00539C;
}

.story .row.story-left,
.story .row.story-right .story-date {
  text-align: right;
}

.story .row.story-right,
.story .row.story-left .story-date {
  text-align: left;
}

.story .story-date {
  padding: 0 5px;
  font-size: 14px;
  font-weight: 400;
  margin: 41px 0 0 0;
}

.story .story-date::after {
  content: '';
  display: block;
  position: absolute;
  width: 17px;
  height: 17px;
  top: 42px;
  background: #FFD662;
  border: 1px solid #00539C;
  border-radius: 15px;
  z-index: 1;
}

.story .row.story-left .story-date::after {
  left: -8.5px;
}

.story .row.story-right .story-date::after {
  right: -8.5px;
}

.story .story-box,
.story .story-launch {
  position: relative;
  display: inline-block;
  margin: 15px;
  padding: 20px 25px;
  border: 1px solid #dddddd;
  border-radius: 6px;
  background: #ffffff;
}

.story .story-launch {
  width: 100%;
  margin: 15px 0;
  padding: 0;
  border: none;
  text-align: center;
  background: transparent;
}

.story .story-box::after,
.story .story-box::before {
  content: '';
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
}

.story .row.story-left .story-box::after,
.story .row.story-left .story-box::before {
  left: 100%;
}

.story .row.story-right .story-box::after,
.story .row.story-right .story-box::before {
  right: 100%;
}

.story .story-launch .story-box::after,
.story .story-launch .story-box::before {
  left: 50%;
  margin-left: -10px;
}

.story .story-box::after {
  top: 26px;
  border-color: transparent transparent transparent #ffffff;
  border-width: 10px;
}

.story .story-box::before {
  top: 25px;
  border-color: transparent transparent transparent #dddddd;
  border-width: 11px;
}

.story .row.story-right .story-box::after {
  border-color: transparent #ffffff transparent transparent;
}

.story .row.story-right .story-box::before {
  border-color: transparent #dddddd transparent transparent;
}

.story .story-launch .story-box::after {
  top: -20px;
  border-color: transparent transparent #dddddd transparent;
}

.story .story-launch .story-box::before {
  top: -19px;
  border-color: transparent transparent #ffffff transparent;
  border-width: 10px;
  z-index: 1;
}

.story .story-box .story-text {
  position: relative;
  float: left;
}

.story .story-launch .story-text {
  max-width: 400px;
}

.story .story-text h3 {
  color: #00539C;
  font-size: 18px;
  font-weight: 400;
}

.story .story-text p {
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 0;
}

@media (max-width: 768px) {
  .story .story-continue::before {
      left: 48.5px;
  }
  
  .story .story-end,
  .story .story-start,
  .story .story-year,
  .story .row.story-left,
  .story .row.story-right .story-date,
  .story .row.story-right,
  .story .row.story-left .story-date,
  .story .story-launch {
      text-align: left;
  }
  
  .story .story-date {
      padding: 0;
  }
  
  .story .row.story-left .story-date::after,
  .story .row.story-right .story-date::after {
      left: 54.5px;
  }
  
  .story .story-box,
  .story .row.story-right .story-date,
  .story .row.story-left .story-date {
      margin-left: 77px;
  }
  
  .story .story-launch .story-box {
      margin-left: 0;
  }
  
  .story .row.story-left .story-box::after {
      left: -20px;
      border-color: transparent #ffffff transparent transparent;
  }

  .story .row.story-left .story-box::before {
      left: -22px;
      border-color: transparent #dddddd transparent transparent;
  }
  
  .story .story-launch .story-box::after,
  .story .story-launch .story-box::before {
      left: 30px;
      margin-left: 0;
  }
}


/*******************************/
/******** Portfolio CSS ********/
/*******************************/
.portfolio {
  position: relative;
  padding: 45px 0;
  background: #ffffff;
}

.portfolio #portfolio-flters {
  padding: 0;
  margin: -15px 0 25px 0;
  list-style: none;
  font-size: 0;
  text-align: center;
}

.portfolio #portfolio-flters li,
.portfolio .load-more .btn {
  cursor: pointer;
  display: inline-block;
  margin: 5px;
  padding: 12px 15px;
  color: #000000;
  font-size: 14px;
  text-transform: uppercase;
  border-radius: 5px;
  background: #FFD662;
  transition: .3s;
}

.portfolio #portfolio-flters li:hover,
.portfolio #portfolio-flters li.filter-active {
  background: #00539C;
  color: #ffffff;
}

.portfolio .load-more {
  text-align: center;
}

.portfolio .load-more .btn {
  padding: 15px 35px;
  font-size: 16px;
  transition: .3s;
}

.portfolio .load-more .btn:hover {
  color: #ffffff;
  background: #00539C;
}

.portfolio .portfolio-item {
  position: relative;
  margin-bottom: 30px;
  border-radius: 5px;
  overflow: hidden;
}

.portfolio .portfolio-item figure {
  background: #ffffff;
  overflow: hidden;
  height: 400px;
  position: relative;
  margin: 0; 
}

.portfolio .portfolio-item figure img {
  width: 100%; 
  height: 100%; 
  object-fit: cover;
  border-radius: 5px;
}

.portfolio .portfolio-item figure .link-preview,
.portfolio .portfolio-item figure .link-details {
  position: absolute;
  display: inline-block;
  opacity: 0;
  line-height: 1;
  text-align: center;
  width: 80px;
  height: 80px;
  top: -50px;
  background: #ffffff;
  transition: .3s;
}

.portfolio .portfolio-item figure .link-preview {
  left: -50px;
  border-radius: 0 80px;
}

.portfolio .portfolio-item figure .link-details {
  right: -50px;
  border-radius: 80px 0;
}

.portfolio .portfolio-item figure:hover .link-preview {
  top: 0;
  left: 0;
}

.portfolio .portfolio-item figure:hover .link-details {
  top: 0;
  right: 0;
}

.portfolio .portfolio-item figure .link-preview i,
.portfolio .portfolio-item figure .link-details i {
  color: #00539C;
  padding-top: 25px;
  font-size: 30px;
}

.portfolio .portfolio-item figure .link-preview:hover,
.portfolio .portfolio-item figure .link-details:hover {
  background: #00539C;
}

.portfolio .portfolio-item figure .link-preview:hover i,
.portfolio .portfolio-item figure .link-details:hover i {
  color: #ffffff;
}

.portfolio .portfolio-item figure:hover .link-preview,
.portfolio .portfolio-item figure:hover .link-details {
  opacity: 1;
}

.portfolio .portfolio-item figure .portfolio-title {
  position: absolute;
  margin: 0;
  width: 100%;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 20px;
  text-align: center;
  color: #000000;
  font-size: 16px;
  text-transform: uppercase;
  background: #FFD662;
  border-radius: 0 0 5px 5px;
  transition: .3s;
}

.portfolio .portfolio-item figure .portfolio-title:hover {
  text-decoration: none;
  background: #00539C;
  color: #ffffff;
}


/*******************************/
/********* Contact CSS *********/
/*******************************/
.contact {
  position: relative;
  width: 100%;
  padding: 45px 0;
}

.contact .contact-form input {
  height: 60px;
  border-radius: 5px;
}

.contact .contact-form .btn {
  height: 60px;
  padding: 0 20px;
  color: #000000;
  font-size: 18px;
  text-transform: uppercase;
  background: #FFD662;
  border-radius: 5px;
  transition: .3s;
}

.contact .contact-form .btn:hover {
  color: #ffffff;
  background: #00539C;
}


/*******************************/
/******* Single Page CSS *******/
/*******************************/
.single {
  position: relative;
  width: 100%;
  padding: 45px 0 65px 0;
}

.single h1,
.single h2,
.single h3,
.single h4,
.single h5,
.single h6 {
  font-weight: 400;
}

.single img {
  margin: 10px 0 15px 0;
}

.single .table,
.single .list-group {
  margin-bottom: 30px;
}

.single .section-header h2 {
  font-weight: 100;
}


/*******************************/
/********* Footer CSS **********/
/*******************************/
.footer {
  position: relative;
  margin-top: 45px;
  padding-top: 90px;
  background: #00539C;
  color: #ffffff;
  font-weight: 300;
}

.footer .footer-contact,
.footer .footer-link,
.footer .footer-form {
  position: relative;
  margin-bottom: 45px;
}

.footer .footer-contact h2,
.footer .footer-link h2,
.footer .footer-form h2{
  position: relative;
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 600;
  color: #fe4b7e;
  letter-spacing: 1px;
}

.footer-form .h2 {
  text-align: center;
}

.footer .footer-contact p {
  font-size: 14px;
}

.footer .footer-contact p i {
  width: 25px;
}

.footer .footer-social {
  position: relative;
  margin-top: 25px;
}

.footer .footer-social a {
  display: inline-block;
}

.footer .footer-social a i {
  margin-right: 15px;
  font-size: 25px;
  color: #ffffff;
}

.footer .footer-social a:last-child i {
  margin: 0;
}

.footer .footer-social a:hover i {
  color: #fe4b7e;
  font-size: 30px;
}

.footer .footer-link a {
  display: block;
  margin-bottom: 10px;
  font-size: 14px;
  color: #ffffff;
  
}

.footer .footer-link a::before {
  position: relative;
  content: "\f0da";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  margin-right: 10px;
}

.footer .footer-link a:hover {
  color: #fe4b7e;
  font-size: 15px;
}

.footer .btn {
  /* position: absolute; */
  position: relative;
  width: 100%;
  right: 5px;
  bottom: 5px;
  font-size: 14px;
  font-weight:400;
  text-transform: uppercase;
  color: #000000;
  background: #ffffff;
  border-radius: 5px;
  transition: .3s;
}

.footer .footer-form .btn:hover {
  color: #ffffff;
  background: #fe4b7e;
}

.footer .footer-menu {
  position: relative;
  padding: 15px 0;
  border-top: 1px solid rgba(255, 255, 255, .1);
}

.footer .footer-menu .f-menu {
  font-size: 0;
  text-align: center;
}

.footer .footer-menu .f-menu a {
  color: #ffffff;
  font-size: 14px;
  margin-right: 15px;
  padding-right: 15px;
  border-right: 1px solid #ffffff;
}

.footer .footer-menu .f-menu a:hover {
  color: #FFD662;
}

.footer .footer-menu .f-menu a:last-child {
  margin-right: 0;
  padding-right: 0;
  border-right: none;
}


.footer .copyright {
  padding: 30px 15px;
  border-top: 1px solid rgba(255, 255, 255, .1);
}

.footer .copyright p {
  margin: 0;
  font-size: 14px;
}

.footer .copyright .col-md-6:last-child p {
  text-align: right;
}

.footer .copyright p a {
  color: #FFD662;
  font-weight: 400;
}

.footer .copyright p a:hover {
  color: #ffffff;
}

@media (max-width: 768px) {
  .footer .copyright p,
  .footer .copyright .col-md-6:last-child p {
      margin: 5px 0;
      text-align: center;
  }
}